@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: ProximaNova;
  src: url(./assets/fonts/ProximaNova-Bold.woff) format('woff'),
       url(./assets/fonts/ProximaNova-Bold.eot) format('eot'),
       url(./assets/fonts/ProximaNova-Regular.woff) format('woff'),
       url(./assets/fonts/ProximaNova-Regular.eot) format('eot'),
       url(./assets/fonts/ProximaNova-Semibold.woff) format('woff'),
       url(./assets/fonts/ProximaNova-Semibold.eot) format('eot');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}